


.editor-box {
	position: fixed;
	left: 0;
	top: 0;

	background: var(--background-main);
	color: #fff;


/*	aspect-ratio: 1.9/1;*/

/*	border: 1px red solid;*/


	height: 100vh;

	stroke-width: var(--power-size);
	fill: transparent;
/*	width: 100vw;*/

}

.editor-box-overlay {
	pointer-events: none;
	position: fixed;
	left: 0;
	top: 0;
	opacity: .5;
	stroke-width: var(--power-size);
	height: 100vh;
	fill: transparent;
}

path {
	stroke-linecap: round;
	stroke-linejoin: round;
}

