.bloor1 {
	filter: blur(1px) brightness(1.5);
	mix-blend-mode: plus-lighter;
	pointer-events: none;
}
.bloor2 {
	filter: blur(2px) brightness(1);
	mix-blend-mode: plus-lighter;
	pointer-events: none;
}

.label {
	text-align: center;
	text-anchor: middle;
	dominant-baseline: middle;
	font-size: 3px;
	user-select: none;
	pointer-events: none;
	fill: var(--label-color);
    font-family: sans-serif;
}
